import React from 'react';
import { Container, Row } from 'react-bootstrap';

import flutter from '../assets/images/flutter.jpg';
import crawler from '../assets/images/crawler.jpg';
import shell from '../assets/images/shell.png';
import Card from './Card';

class Carousel extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            items: [
                {
                    id: 0,
                    title: 'Inffecto',
                    subTitle: 'covid-19 safety android app',
                    imgSrc: flutter,
                    link: 'https://github.com/ShrutiKatpara/inffecto',
                    selected: false
                },
                {
                    id: 1,
                    title: 'Concurrent Web Crawler',
                    subTitle: 'A multithreaded web crawler using thread safe data structures',
                    imgSrc: crawler,
                    link: 'https://github.com/ShrutiKatpara/Web-Crawler',
                    selected: false
                },
                {
                    id: 2,
                    title: 'Shell in C',
                    subTitle: '',
                    imgSrc: shell,
                    link: 'https://github.com/ShrutiKatpara/Basic-Shell-in-C',
                    selected: false
                },
                // {
                //     id: 3,
                //     title: 'Minsweeper Game played by AI',
                //     subTitle: '',
                //     imgSrc: shell,
                //     link: 'https://github.com/ShrutiKatpara/Minesweeper-Game',
                //     selected: false
                // },
                // {
                //     id: 4,
                //     title: 'Google PageRank Algorithm',
                //     subTitle: '',
                //     imgSrc: shell,
                //     link: 'https://github.com/ShrutiKatpara/Google-Pagerank-Algo-Implementation',
                //     selected: false
                // },
            ]
        }
    }

    handleCardClick = (id, card) => {
        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach( item => {
            if (item.id !== id) {
                item.selected = false;
            }
        });

        this.setState({
            items
        })
    }

    makeItems = (items) => {

        // var rows = [];
        // for (var i = 0; i < items.length; i+=3)
        // {
        //     var temp = [];
        //     for (var j = i; j < i+3; j+=1)
        //     {
        //         temp.push(<Card item={items[j]} click={(e => this.handleCardClick(items[j].id, e))} key={items[j].id}/>)
        //     }
        //     rows.push(temp);
        // }

        // return rows;
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id}/>
        })
    }

    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }
}

export default Carousel;