import React from 'react';

import Hero from '../components/Hero';
import Content from '../components/Content';
import avatar from '../assets/images/avatar.jpg';

function  Home(props) {
    return(
        <div>
            <div className="header">
            <img className="avatar g-card-info" src={avatar} alt={avatar} />
            </div>
            <Hero subtitle={props.subtitle}/>
            <Content>
            <p> I am junior undergraduate computer science student at IIT Gandhinagar.</p>

            <p>I believe that depicting the results out of any work is vital. To showcase my work effectively, I tried my hands and learned web development and app development. I am highly interested in working on customer friendly applications which can make their lives easier.</p>

            <p>Apart from my prime interest, I am also spending some time reading finance blogs and newsletters, motivational books. Someone well said that, "Learn how to manage your money, before earning it" I am trying to follow the thought.</p>

            <p> Thanks</p>
            </Content>    
        </div>
    );
}


export default Home;