import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Route,
  Link
} from "react-router-dom";
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

import Footer from './components/Footer';
import Home from './pages/Home';
import Projects from './pages/Projects';
import Contact from './pages/Contact';

class App extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      title: 'Shruti Katpara',
      navLinks: [
        {Home: '/'},
        {Projects: '/projects'},
        {Contact: '/contact'}
      ],
      home: {
        title: 'Shruti Katpara',
        subtitle: 'Be consistent'
      },
      projects: {
        title: 'Hi there, have a look at my projects',
      },
      contact: {
        title: 'Let\'s Connect!',
      },
      
    }
  }

  render(){
    return (
      <Router>
        <Container className="p-0" fluid={true}>
        <Navbar bg="dark" variant="dark" expand="lg">
          <Navbar.Brand href="/">Shruti Katpara</Navbar.Brand>
          <Navbar.Toggle className="border-8" aria-controls="navbar-toggle"></Navbar.Toggle>
          <Navbar.Collapse id="navbar-toggle" >
            <Nav className="ml-auto">
              <Link className="nav-link" to="">Home</Link>
              <Link className="nav-link" to="/projects">Projects</Link>
              <Link className="nav-link" to="/contact">Contact</Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>

        <Route path="/" exact render={()=> <Home title={this.state.home.title} subtitle={this.state.home.subtitle}/>}></Route>
        <Route path="/projects" exact render={()=> <Projects title={this.state.projects.title} />}></Route>
        <Route path="/contact" exact render={()=> <Contact title={this.state.contact.title} />}></Route>
        <Footer/>

        </Container>
      </Router>

      
    );
  }
}

export default App;
